<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="container pt-5">
      <div class="row justify-content-center">
        <div class="col-12 col-md-10">
          <ul class="list-unstyled form-row justify-content-around">
            <li class="col-12 col-md-4 mb-3">
              <span class="checkStep d-block text-center">1. 填寫訂購資料</span>
            </li>
            <li class="col-12 col-md-4 mb-3">
              <span
                class="checkStep d-block text-center"
                :class="{ active: order.is_paid == false }"
                >2. 付款</span
              >
            </li>
            <li class="col-12 col-md-4 mb-3">
              <span
                class="checkStep d-block text-center"
                :class="{ active: order.is_paid == true }"
                >3. 完成</span
              >
            </li>
          </ul>
        </div>
      </div>
    </div>

    <div class="container">
      <div class="row justify-content-center">
        <form class="col-12 col-md-8" @submit.prevent="payOrder">
          <h2 class="text-center">確認訂購資料</h2>
          <table class="table">
            <thead>
              <th>品名</th>
              <th>數量</th>
              <th>單價</th>
            </thead>
            <tbody>
              <tr v-for="item in order.products" :key="item.id">
                <td class="align-middle">{{ item.product.title }}</td>
                <td class="align-middle">
                  {{ item.qty }}{{ item.product.unit }}
                </td>
                <td class="align-middle text-right">
                  {{ item.final_total | currency }}
                </td>
              </tr>
            </tbody>
            <tfoot>
              <tr>
                <td colspan="2" class="text-right">總計:</td>
                <td class="text-right">{{ order.total | currency }}</td>
              </tr>
            </tfoot>
          </table>
          <table class="table">
            <tbody>
              <tr>
                <th width="140">Email</th>
                <td>{{ order.user.email }}</td>
              </tr>
              <tr>
                <th>姓名</th>
                <td>{{ order.user.name }}</td>
              </tr>
              <tr>
                <th>收件人電話</th>
                <td>{{ order.user.tel }}</td>
              </tr>
              <tr>
                <th>收件人地址</th>
                <td>{{ order.user.address }}</td>
              </tr>
              <tr>
                <th>付款狀態</th>
                <td>
                  <span v-if="!order.is_paid">尚未付款</span>
                  <span v-else class="text-success">付款完成</span>
                </td>
              </tr>
            </tbody>
          </table>
          <div class="text-right mb-3" v-if="order.is_paid === false">
            <button class="btn btn-outline-primary">確認付款去</button>
          </div>
        </form>
        <div class="col-12 mb-3" v-if="order.is_paid">
          <button
            class="btn btn-outline-primary btn-block py-3"
            @click.prevent="$router.push(`/product`)"
          >
            繼續買起來
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      orderId: '',
      order: {
        user: {},
      },
      isLoading: false,
    };
  },
  methods: {
    getOrder() {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order/${vm.orderId}`;
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        vm.order = response.data.order;
        vm.isLoading = false;
        console.log('這是詳細訂單資料', response.data);
      });
    },
    payOrder() {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/pay/${vm.orderId}`;
      this.$http.post(api).then((response) => {
        if (response.data.success) {
          console.log('完成付款', response.data);
          vm.getOrder();
        }
      });
    },
  },
  created() {
    this.orderId = this.$route.params.id;
    this.getOrder();
  },
};
</script>
